:root {
  --bp-lg: 70em;
  --bp-md: 50em;
  --bp-sm: 30em;
}

html,
body {
  background-color: var(--white);
  box-sizing: border-box;
  color: #444;
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
  position: relative;
  scroll-behavior: smooth;
}

header {
  background-color: #28324A;
  background-image: url("../img/header-bg-720X1280.jpg");
  background-attachment: fixed;
  background-position: center right;
  background-size: cover;
  margin: 0;
  padding: 0;
  position: relative;
  height: 100vh;
  max-height: calc((9 / 16) * 100%);
  width: 100%;
}

main {
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
}

section {
  margin: 0;
  min-height: 75vh;
  padding: 0;
  width: 100%;
}

h1 {
  font-size: calc(54px + (105 - 54) * ((100vw - 300px) / (1600 - 300)));
  font-family: 'Prata', serif;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 1.1em;
  letter-spacing: 0.05em;
  margin: 0;
}

h2 {
  font-family: 'Great Vibes', serif;
  font-size: 60px;
  font-size: calc(54px + (66 - 54) * ((100vw - 300px) / (1400 - 300)));
  font-weight: 400;
  margin: 0 0 15px 0;
  text-align: center;
}

p {
  font-family: 'Raleway', sans-serif;
  line-height: 1.418;
  margin: 0 auto;
  max-width: 800px;
  text-align: center;
  width: 95%;
}

a {
  color: inherit;
  text-decoration: none;
  text-transform: inherit;
  font-size: inherit;
  font-weight: inherit;
  position: relative;
}

@media only screen and (min-width: 30em) {
  header {
    background-image: url("../img/_MG_6898.jpg");
    background-position: center right;
  }
}

@media only screen and (min-width: 600px) {
  .grid--2-col  p {
    max-width: 70%;
  }
}

#canvas-image-blending {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 80px 0;
  text-align: center;
}

.grid--2-col {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-template-areas: "lft" "rgt";
  min-height: 480px;
}

.grid-item--lft {
  grid-area: lft;
}

.grid-item--rgt {
  grid-area: rgt;
}

@media only screen and (min-width: 50em) {
  .grid--2-col {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: "lft rgt";
  }
}

.locations {
  width: 100%;
  height: 380px;
  position: relative;
  overflow: hidden;
}

#map {
  height: 380px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

#photographs .container {
  padding-top: 60px;
}

.gallery {
  display: grid;
  grid-template-columns: 95%;
  grid-auto-rows: 50vw;
  grid-gap: 10px;
  grid-auto-flow: dense;
  justify-content: center;
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

.gallery-item {
  background-color: var(--white);
  box-shadow: 1px 2px 6px rgba(100, 100, 100, 0.6);
  overflow: hidden;
  padding: 4px;
  border-radius: 2px;
}

.gallery-item img {
  width: 100%;
  height: 100%;
  opacity: 0.73;
  object-fit: cover;
}

.img-lg,
.img-tl,
.img-sm {
  grid-column: span 1;
  grid-row: span 1;
}

@media only screen and (min-width: 40em) {
  .gallery {
    grid-template-columns: repeat(auto-fill, 20vw);
    grid-gap: 12px;
    grid-auto-rows: 20vw;
  }

  .img-lg {
    grid-column: span 2;
    grid-row: span 2;
  }

  .img-tl {
    grid-column: span 1;
    grid-row: span 2;
  }

  .img-sm {
    grid-column: span 1;
    grid-row: span 1;
  }
}

@media only screen and (min-width: 70em) {
  .gallery {
    grid-template-columns: repeat(auto-fill, 250px);
    grid-gap: 12px;
    grid-auto-rows: 150px;
  }
}

.menu {
  background-color: var(--blue);
  font-family: 'Raleway';
  font-size: 18px;
  font-weight: 200;
  letter-spacing: 1px;
  left: 0;
  display: block;
  position: fixed;
  top: -110%;
  width: 100%;
  z-index: 700;
  transition: all 0.3s;
  height: 100vh;
}

.nav-btn {
  border-radius: 5px;
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  right: 15px;
  top: 10px;
  z-index: 999;
  transition: all 0.5s;
}

.nav-btn img {
  width: 100%;
  height: auto;
}

.menu.open {
  top: 0;
}

.nav-btn.pressed {
  width: 40px;
  height: 40px;
  position: fixed;
  background-color: var(--blue);
  background-image: url("../img/close.svg");
  background-size: contain;
}

.nav-btn.pressed img {
  opacity: 0;
}

.main-nav {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 50px 0 0;
  flex-direction: column;
  justify-content: space-around;
  height: 80vh;
}

.main-nav .nav-item {
  padding: 5px;
  margin: 0 7px;
  text-align: center;
}

.nav-item {
  color: var(--white);
  display: inline-block;
  margin: 0 6px;
  position: relative;
  text-transform: uppercase;
}

.nav-item a::after {
  content: '';
  display: block;
  height: 2px;
  width: 0;
  background-color: var(--gold);
  position: absolute;
  left: 50%;
  bottom: -4px;
  transform: translateX(-50%);
  transition: width 0.4s;
}

.nav-item a:hover::after,
.nav-item a:active::after {
  width: 100%;
}

@media only screen and (min-width: 55em) {
  .menu {
    background-color: transparent;
    position: absolute;
    top: 0;
  }

  .main-nav {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0;
    height: 80px;
  }

  .nav-btn {
    display: none;
  }
}

/*-----------------------------------------------------------------------*/

/*-------------------------GENERAL ELMNTS/STYLES-------------------------*/

/*-----------------------------------------------------------------------*/

:root {
  --blue: #28324A;
  --blush: #FFECE5;
  --gold: #D6A319;
  --dark-gold: #CC9C1A;
  --black: #444444;
  --white: #FAFAFA;
}

.btn {
  background-color: var(--gold);
  border: 2px solid var(--gold);
  border-radius: 2px;
  color: var(--white);
  font-size: 16px;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 1px;
  margin: 40px auto 0;
  padding: 13px;
  min-width: 175px;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.2s;
}

.btn-ghost {
  background-color: transparent;
  color: var(--gold);
  padding: 13px;
  margin-top: 10px;
}

.btn:hover {
  background-color: var(--dark-gold);
  color: var(--white);
}

.hide {
  display: none !important;
}

.show {
  display: block;
}

.map-hide {
  overflow: hidden;
  height: 0;
}

.map-show {
  display: flex;
  height: 100%;
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------HEADER------------------------------*/

/*-----------------------------------------------------------------------*/

header:before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  background-color: var(--blue);
  opacity: 0.45;
}

.names {
  color: var(--white);
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  text-align: center;
  transform: translateY(-50%);
}

.name--colin {
  width: 100%;
  text-align: center;
}

.and {
  font-family: 'Great Vibes';
  color: var(--gold);
  font-size: 1.3em;
}

.full-date {
  color: var(--white);
  font-size: 28px;
  font-size: calc(22px + (30 - 22) * ((100vw - 360px) / (1600 - 360)));
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
  letter-spacing: 0.2em;
  text-align: center;
  position: absolute;
  top: 77%;
  top: calc(100vh - 25vh);
  width: 100%;
  text-align: center;
  transform: translateY(-50%);
}

.full-date::before {
  background-color: var(--white);
  content: '';
  opacity: 0.6;
  display: block;
  height: 1px;
  border: none;
  width: 100%;
  position: absolute;
  left: 50%;
  bottom: 1.2em;
  transform: translateX(-50%);
}

.full-date::after {
  background-color: var(--white);
  content: '';
  opacity: 0.6;
  display: block;
  height: 1px;
  border: none;
  width: 100%;
  position: absolute;
  left: 50%;
  bottom: -5px;
  transform: translateX(-50%);
}

@media only screen and (min-width: 40em) {
  .names {
    left: 12.25%;
    text-align: left;
    top: 47%;
    width: 60%;
  }

  .name--colin {
    text-align: right;
  }

  .full-date {
    top: calc(100vh - 20vh);
    max-width: 12em;
    left: 50%;
    transform: translateX(-50%);
  }

  .full-date::before {
    width: 105%;
  }

  .full-date::after {
    width: 95%;
  }
}

@media only screen and (min-width:50em) {
  .names {
    left: 17%;
    width: 47%;
  }
}

/*-----------------------------------------------------------------------*/

/*                                   WELCOME                             */

/*-----------------------------------------------------------------------*/

.countdown {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.date {
  border-radius: 2px;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 30px 1fr;
  justify-items: center;
  width: 100px;
  height: 95px;
  background-color: var(--blue);
  margin: 60px 10px 0;
  box-shadow: 4px 4px 18px rgba(0, 0, 0, 0.2), 2px 2px 12px rgba(0, 0, 0, 0.5);
}

.date-title {
  color: var(--blush);
  align-self: end;
  font-family: 'Raleway';
  font-size: 16px;
  font-weight: 200;
  padding: 0;
  margin: 0;
  letter-spacing: 1.4px;
  text-transform: uppercase;
}

[class*="date--"] {
  font-family: 'Prata';
  font-size: 48px;
  align-self: center;
  color: var(--gold);
}

.hours,
.minutes,
.seconds {
  display: none;
}

@media only screen and (min-width: 30em) {
  .hours {
    display: grid;
  }
}

@media only screen and (min-width: 44em) {
  .minutes,
  .seconds {
    display: grid;
  }
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------EVENT------------------------------*/

/*-----------------------------------------------------------------------*/

.event-content {
  color: var(--white);
  text-align: center;
  background-color: var(--blue);
}

.event-img {
  display: none;
}

.event-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media only screen and (min-width: 50em) {
  .event-img {
    display: block;
  }
}

.schedule {
  list-style: none;
  margin: 30px 0 0;
  padding: 0;
}

.event {
  letter-spacing: 0.5px;
  margin: 12px 0;
}

/*-----------------------------------------------------------------------*/

/*-------------------------------Accommodations----------------------------*/

/*-----------------------------------------------------------------------*/

.map-info {
  background-color: rgba(40, 50, 74, 0.85);
  color: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  transition: all 0.4s;
  width: 100%;
  z-index: 99;
}

.map-info-body {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
  align-content: center;
  width: 40%;
  position: relative;
}

@media only screen and (max-width: 1100px) {
  .map-info-body {
    width: 80%;
    border: none;
    border-radius: 0;
  }

  .map-info-body p {
    max-width: 580px;
  }
}

@media only screen and (max-width: 700px) {
  .map-info-body {
    width: 100%;
  }
}

.map-info-close {
  display: block;
  height: 40px;
  width: 40px;
  position: absolute;
  right: 15px;
  top: 15px;
  z-index: 350;
}

.map-info-close::before,
.map-info-close::after {
  display: block;
  content: '';
  width: 30px;
  height: 2px;
  background-color: var(--white);
  position: absolute;
  top: 18px;
  right: 5px;
}

.map-info-close::before {
  transform: rotate(45deg);
}

.map-info-close::after {
  transform: rotate(-45deg);
}

.map-info-title h4 {
  font-family: 'Great Vibes', sans-serif;
  font-size: 32px;
  font-weight: 200;
  margin: 24px 0 12px;
  text-align: center;
}

.map-info-content {
  padding: 0 40px;
  display: flex;
  font-weight: 200;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 500px) {
  .map-info-content {
    width: 100%;
    padding: 0;
  }
}

.map-btns {
  margin: 20px auto 25px;
}

.map-info-btn {
  display: inline-block;
  text-decoration: none;
  padding: 8px 32px;
  border-radius: 2px;
  border: 2px solid var(--gold);
  color: var(--gold);
  text-transform: uppercase;
  font-weight: 400;
  background-color: transparent;
  margin: 0 6px;
  transition: all 0.3s;
  min-width: 96px;
  text-align: center;
}

.map-info-btn:hover {
  background-color: var(--gold);
  color: var(--white);
}

@media only screen and (max-width: 440px) {
  .map-info-title {
    margin-top: 24px;
  }

  .map-info-btn {
    display: block;
    margin: 6px auto;
  }
}

/*-----------------------------------------------------------------------*/

/*-------------------------------THINGS TO DO----------------------------*/

/*-----------------------------------------------------------------------*/

.boston {
  color: var(--white);
  background-image: linear-gradient(rgba(40, 50, 74, 0.8), rgba(75, 85, 99, 0.5)), url("https://source.unsplash.com/1600x900/?boston, city");
  background-size: cover;
}

.cape-cod {
  color: var(--white);
  background-image: linear-gradient(rgba(214, 123, 25, 0.6), rgba(40, 50, 74, 0.4)), url("https://source.unsplash.com/1600x900/?beach");
  background-size: cover;
}

/*-----------------------------------------------------------------------*/

/*-----------------------------------GALLERY-----------------------------*/

/*-----------------------------------------------------------------------*/

/*-----------------------------------------------------------------------*/

/*-----------------------------------FOOTER------------------------------*/

/*-----------------------------------------------------------------------*/

footer {
  background-color: var(--white);
  background-image: linear-gradient(rgba(30, 40, 64, 0.85), rgba(30, 40, 64, 0.75)), url("../img/squirrel-fullsize.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  color: var(--white);
  padding: 20px 0;
  min-height: 50vh;
  position: relative;
}

form {
  margin: 5px auto;
  text-align: center;
  padding: 5px 7px;
}

input {
  margin: 4px auto;
  font-size: 16px;
  color: var(--white);
  background-color: transparent;
  border: none;
  border-bottom: 2px solid var(--gold);
  border-left: 2px solid var(--gold);
  border-radius: 0;
  padding: 8px;
  letter-spacing: 1px;
  width: 90%;
}

input:focus {
  background-color: rgba(40, 50, 75, 0.5);
  outline: none;
}

button.frm-btn {
  border: none;
  height: auto;
  margin: 4px 0;
  font-size: 14px;
  padding: 10px 16px;
  border: 2px solid var(--gold);
  background-color: transparent;
  color: var(--gold);
  width: 95%;
}

.footer-nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 4px auto;
  padding: 0;
  max-width: 750px;
}

.footer-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 60px 0 20px;
}

.footer-nav .nav-item {
  font-size: 14px;
  margin: 4px 20px;
  padding: 6px;
}

.thanks {
  font-size: 14px;
  position: relative;
}

.thanks > a {
  color: var(--gold);
  text-decoration: none;
  text-transform: uppercase;
}

@media only screen and (min-width: 50em) {
  footer {
    background-size: contain;
    background-position: right;
  }

  input {
    margin-right: 18px;
    width: 250px;
  }

  button.frm-btn {
    width: 200px;
  }

  .thanks {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.img-box {
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  opacity: 0;
  animation-duration: 0.6s;
  transition: all 0.3s;
}

.img-box.animated {
  z-index: -1;
}

.img-box.animated.zoomIn {
  z-index: 999;
  opacity: 1;
}

.img-box-controls {
  position: absolute;
  bottom: 30px;
  display: flex;
  justify-content: space-between;
  left: 50%;
  height: 50px;
  width: 150px;
  transform: translateX(-50%);
}

.control {
  height: 50px;
  width: 50px;
  border-radius: 20%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.15);
}

.img-box-close {
  position: absolute;
  top: 25px;
  right: 25px;
  height: 50px;
  width: 50px;
}

.img-box-close img,
.control img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 75%;
  height: auto;
  opacity: 0.85;
}

.control-rgt img {
  transform: rotateY(180deg)  translate(50%, -50%);
}

.img-box-content {
  height: auto;
  width: 95%;
  position: relative;
  object-fit: cover;
}

.img-box-content img {
  width: 100%;
  opacity: 0.95;
  height: auto;
}

.img-box-close:hover img,
.control:hover img {
  opacity: 1;
}

@media only screen and (min-width: 40em) {
  .img-box-content {
    height: 70vh;
    width: auto;
    max-width: 90%;
    overflow: hidden;
    object-fit: cover;
  }

  .img-box-content img {
    width: auto;
    height: 100%;
  }
}